import React from 'react';
import ReactDOM from 'react';
import { DateTime } from 'luxon';
import { ee } from '../../index.js';
import { interactWithTweet, undoTweetInteraction } from '../../remote';
import QuotedTweet from './quoted-tweet/quoted-tweet.js';

class Tweet extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      timeDateStamp: null
    };
  }

  componentWillMount() {
    this.setState({
      timeDateStamp: this.getTimeString(),
      retweetCount: this.props.retweetCount,
      favoriteCount: this.props.favoriteCount,
      retweeted: this.props.retweeted,
      favorited: this.props.favorited
    });
    setTimeout(() => {
      setInterval(() => {
        this.setState({
          timeDateStamp: this.getTimeString()
        });
        console.log('updated timestamps');
      }, 30000);
    }, 30000);

    ee.on(this.props.tweetId, updatedCount => {
      console.log('event found');
      this.setState({
        retweetCount: updatedCount.retweets,
        favoriteCount: updatedCount.favs
      });
    });
  }

  formatTwitterTime(twitterTime) {
    function getMonthN(monthStr) {
      switch (monthStr) {
        case 'Jan':
          return 1;
        case 'Feb':
          return 2;
        case 'Mar':
          return 3;
        case 'Apr':
          return 4;
        case 'May':
          return 5;
        case 'Jun':
          return 6;
        case 'Jul':
          return 7;
        case 'Aug':
          return 8;
        case 'Sep':
          return 9;
        case 'Oct':
          return 10;
        case 'Nov':
          return 11;
        case 'Dec':
          return 12;
        default:
          return 'NaM';
      }
    }

    const hhmmss = twitterTime[3].split(':');
    const formattedTTime = {
      month: getMonthN(twitterTime[1]),
      day: Number(twitterTime[2]),
      hour: Number(hhmmss[0]),
      minute: Number(hhmmss[1]),
      second: Number(hhmmss[2]),
      year: Number(twitterTime[5]),
      zone: 'utc'
    };
    return formattedTTime;
  }

  getRecentTimeStr(twitTime, curTime) {
    const diff = curTime - twitTime;

    if (diff < 60) {
      return Math.round(diff) + 's';
    } else if (diff < 600) {
      return (diff / 60).toString()[0] + 'm';
    } else if (diff < 3600) {
      const min = (diff / 60).toString();
      return min[0] + min[1] + 'm';
    } else if (diff < 36000) {
      const h = (diff / 3600).toString();
      return h[0] + 'h';
    } else if (diff < 86400) {
      const h = (diff / 3600).toString();
      return h[0] + h[1] + 'h';
    }
  }

  getDate(twitterTime) {
    let timeString;
    if (twitterTime[2].charAt(0) === '0') {
      twitterTime[2] = twitterTime[2].charAt(1);
    }
    if (twitterTime[5] === new Date().getFullYear().toString()) {
      timeString = twitterTime[1] + ' ' + twitterTime[2];
    } else {
      timeString = twitterTime[2] + ' ' + twitterTime[1] + ' ' + twitterTime[5];
    }
    return timeString;
  }

  getTimeString() {
    const twitterTime = this.props.time.split(' ');

    const formattedTTime = this.formatTwitterTime(twitterTime);

    const result = this.getRecentTimeStr(
      DateTime.fromObject(formattedTTime).toSeconds(),
      DateTime.local()
        .toUTC()
        .toSeconds()
    );

    if (result === undefined) {
      return this.getDate(twitterTime);
    }

    // console.log(result);
    return result;
  }

  getInteractionCountString(number) {
    const numString = number.toString();

    if (number === 0) {
      return '';
    } else if (number > 999999) {
      number = `${numString[0]}.${numString[1]}M`;
    } else if (number > 99999) {
      number = `${numString[0]}${numString[1]}${numString[2]}K`;
    } else if (number > 9999) {
      number = `${numString[0]}${numString[1]}K`;
    } else if (number > 999) {
      number = `${numString[0]}.${numString[1]}K`;
    }
    return number;
  }

  getExpandedURL(shortenedURL) {
    const urlEntities = this.props.urls;
    if (urlEntities === undefined) return;

    let urlObj;

    urlEntities.forEach(urlEntity => {
      if (urlEntity.url === shortenedURL) {
        urlObj = {
          display: urlEntity.display_url,
          full: urlEntity.expanded_url
        };
      }
    });

    return urlObj;
  }

  filterText(text) {
    const separatedBySpaces = text.split(' ');
    let filteredArray = [];
    separatedBySpaces.forEach(string => {
      if (!string.includes('https://t.co/')) {
        if (string[0] === '@') {
          const handle = string.slice(1);
          filteredArray.push(
            <a
              class="blue highlight"
              target="_blank"
              rel="noopener noreferrer"
              href={`https://twitter.com/${handle}`}
            >
              {string}
            </a>
          );
          filteredArray.push(' ');
        } else if (string[0] === '#') {
          const hashtag = string.slice(1);
          filteredArray.push(
            <a
              class="blue highlight"
              target="_blank"
              rel="noopener noreferrer"
              href={`https://twitter.com/search?q=%23${hashtag}`}
            >
              {string}
            </a>
          );
          filteredArray.push(' ');
        } else if (string === '4Ap%8CIkf*k3$5nt$HsT7') {
          filteredArray.push(<br></br>);
        } else {
          filteredArray.push(string);
          filteredArray.push(' ');
        }
      } else {
        const urlObj = this.getExpandedURL(string);

        if (urlObj !== undefined) {
          filteredArray.push(
            <a
              class="blue highlight"
              target="_blank"
              rel="noopener noreferrer"
              href={urlObj.full}
            >
              {urlObj.display}
            </a>
          );
          filteredArray.push(' ');
        }
      }
    });
    return filteredArray;
  }

  handleImageClick(index) {
    this.props.openGallery(this.props.media, index);
  }

  setVolume() {
    // const node = ReactDOM.findDOMNode(this);
    // console.log(node.childNodes[1].childNodes);
    // node.childNodes[0].childNodes[0].childNodes.forEach(node => {
    //   if (node.classList.contains('tweet-video-container')) {
    //     node.childNodes[0].volume = this.props.getVolume();
    //   }
    // });
  }

  getMediaDiv() {
    if (this.props.media === undefined) {
      return;
    }

    if (this.props.media[0].type === 'video') {
      let vidUrl;

      const variants = this.props.media[0].video_info.variants;

      variants.forEach(variant => {
        if (variant.bitrate === 832000) {
          vidUrl = variant.url;
        }
      });

      return (
        <div className="tweet-video-container">
          <video
            onLoadStart={() => {
              this.setVolume(this);
            }}
            controls
            loop
            style={{
              borderRadius: '12px',
              marginBottom: '5px',
              border: 'solid 1px rgb(56, 68, 77)',
              maxHeight: '506px',
              width: '506px',
              backgroundColor: 'black',
              outline: 'none'
            }}
          >
            <source src={vidUrl}></source>
          </video>
        </div>
      );
    }

    if (this.props.media.length === 1) {
      return (
        <img
          className="attached-image"
          src={this.props.media[0].media_url}
          alt="media-one"
          onClick={() => this.handleImageClick(0)}
        ></img>
      );
    } else if (this.props.media.length === 2) {
      return (
        <div className="tweet-image-container">
          <img
            className="media-length-two-image-one"
            src={this.props.media[0].media_url}
            onClick={() => this.handleImageClick(0)}
            alt="media-one"
          ></img>
          <div className="media-length-two-divider"></div>
          <img
            className="media-length-two-image-two"
            src={this.props.media[1].media_url}
            onClick={() => this.handleImageClick(1)}
            alt="media-two"
          ></img>
        </div>
      );
    } else if (this.props.media.length === 3) {
      return (
        <div className="media-length-three-container">
          <img
            className="media-length-three-image-one"
            src={this.props.media[0].media_url}
            onClick={() => this.handleImageClick(0)}
            alt="media-one"
          />
          <div className="media-length-three-vert-divider"></div>
          <div className="media-length-three-second-container">
            <img
              className="media-length-three-image-two"
              src={this.props.media[1].media_url}
              onClick={() => this.handleImageClick(1)}
              alt="media-two"
            />
            <div className="media-length-three-hor-divider"></div>
            <img
              className="media-length-three-image-three"
              src={this.props.media[2].media_url}
              onClick={() => this.handleImageClick(2)}
              alt="media-three"
            />
          </div>
        </div>
      );
    } else if (this.props.media.length === 4) {
      return (
        <div className="media-length-four-container">
          <img
            className="media-length-four-image-one"
            src={this.props.media[0].media_url}
            onClick={() => this.handleImageClick(0)}
            alt="media-one"
          />
          <div className="media-length-three-vert-divider"></div>
          <div className="media-length-four-second-container">
            <img
              className="media-length-four-image-two"
              src={this.props.media[1].media_url}
              onClick={() => this.handleImageClick(1)}
              alt="media-two"
            />
            <div className="media-length-four-hor-divider"></div>
            <img
              className="media-length-four-image-three"
              src={this.props.media[2].media_url}
              onClick={() => this.handleImageClick(2)}
              alt="media-three"
            />
            <div className="media-length-four-hor-divider"></div>
            <img
              className="media-length-four-image-four"
              src={this.props.media[3].media_url}
              onClick={() => this.handleImageClick(3)}
              alt="media-four"
            />
          </div>
        </div>
      );
    }
  }

  getTweetHref() {
    const { tweetId, handle } = this.props;
    return `https://twitter.com/${handle}/status/${tweetId}`;
  }

  async handleFavoriteClick() {
    if (this.state.favorited === false) {
      this.setState({ favorited: true });
      console.log(this.props.tweetId);
      const response = await interactWithTweet(this.props.tweetId, 'fav');
      console.log(response);

      if (response.resp.statusCode !== 200) {
        this.setState({ favorited: false });
      }
    } else {
      this.setState({ favorited: false });
      console.log(this.props.tweetId);
      const response = await undoTweetInteraction(this.props.tweetId, 'fav');
      console.log(response);

      if (response.resp.statusCode !== 200) {
        this.setState({ favorited: true });
      }
    }
  }

  async handleRetweetClick() {
    if (this.state.retweeted === false) {
      this.setState({ retweeted: true });
      console.log(this.props.tweetId);
      const response = await interactWithTweet(this.props.tweetId, 'rt');
      console.log(response);

      if (response.resp.statusCode !== 200) {
        this.setState({ retweeted: false });
      }
    } else {
      this.setState({ retweeted: false });
      console.log(this.props.tweetId);
      const response = await undoTweetInteraction(this.props.tweetId, 'rt');
      console.log(response);

      if (response.resp.statusCode !== 200) {
        this.setState({ favorited: false });
      }
    }
  }

  render() {
    let favoriteClassName = 'icon-favorite';
    if (this.state.favorited) {
      favoriteClassName += ' red';
    }

    let retweetClassName = 'icon-retweet';
    if (this.state.retweeted) {
      retweetClassName += ' green';
    }

    return (
      <div>
        {this.props.retweeterHandle !== undefined && (
          <div className="retweet-bar">
            <span class="icon-retweet poop"></span>
            <a
              className="highlight"
              target="_blank"
              rel="noopener noreferrer"
              href={`https://twitter.com/${this.props.retweeterHandle}`}
            >
              <div className="retweeter">{this.props.retweeterName}</div>
            </a>
            <div className="retweeted-text">retweeted</div>
          </div>
        )}

        <div className="tweet-container">
          <a
            className="avatar-profile-anchor highlight"
            target="_blank"
            rel="noopener noreferrer"
            href={`https://twitter.com/${this.props.handle}`}
          >
            <img src={this.props.avatarUrl} alt="avatar" className="avatar" />
          </a>
          <div className="tweet-content">
            <div className="tweet-top-bar">
              <a
                className="tweet-name highlight"
                target="_blank"
                rel="noopener noreferrer"
                href={`https://twitter.com/${this.props.handle}`}
              >
                {this.props.name}
              </a>
              <p className="tweet-at">@</p>
              <p className="tweet-handle">{this.props.handle}</p>
              <div className="tweet-date-dot">.</div>
              <a
                className="tweet-date highlight"
                target="_blank"
                rel="noopener noreferrer"
                href={this.getTweetHref()}
              >
                {this.state.timeDateStamp}
              </a>
            </div>
            <p className="tweet-text">{this.filterText(this.props.text)}</p>
            {this.getMediaDiv()}
            {this.props.quotedTweet !== undefined && (
              <QuotedTweet data={this.props.quotedTweet} />
            )}
            <div className="tweet-action-bar">
              <span
                className={retweetClassName}
                onClick={this.handleRetweetClick.bind(this)}
              ></span>
              <div className="retweet-counter">
                {this.getInteractionCountString(this.state.retweetCount)}
              </div>
              <span
                class={favoriteClassName}
                onClick={this.handleFavoriteClick.bind(this)}
              ></span>
              <div className="favorite-counter">
                {this.getInteractionCountString(this.state.favoriteCount)}
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Tweet;
