import React from 'react';
import { signUpEmail, logIn } from '../../auth';
import { ee } from '../../index';

//  "See whatâ€™s good in the world right now
//
// Join Good Twitter today."

class LoginSignInScreen extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      signUpInitiated: false,
      doneLoading: false,
      fetchingAuthStatus: true
    };
  }

  componentWillMount() {
    ee.on('loading-finish', () => {
      console.log('logged in');
      this.setState({
        doneLoading: true,
        fetchingAuthStatus: false
      });
    });

    ee.on('logout', () => {
      console.log('logged out');
      this.setState({
        doneLoading: false,
        signUpInitiated: false,
        fetchingAuthStatus: false
      });
    });

    ee.on('not-authenticated', () => {
      this.setState({
        fetchingAuthStatus: false
      });
    });
  }

  toggleSignUpInit() {
    const { signUpInitiated } = this.state;

    if (signUpInitiated) {
      this.setState({
        signUpInitiated: false
      });
    } else {
      this.setState({
        signUpInitiated: true
      });
    }
  }

  render() {
    let rightClass = 'login-right-part';
    if (this.state.doneLoading) {
      rightClass += ' moved-right';
    }

    let leftClass = '';
    if (this.state.doneLoading) {
      leftClass += ' moved-left';
    }
    return (
      <div id="login-overlay">
        <div id="login-left-part" className={leftClass}></div>

        <div id="login-right-part" className={rightClass}>
          {(this.state.signUpInitiated === true ||
            this.state.fetchingAuthStatus === true) &&
            this.state.doneLoading === false && (
              <div class="loadingio-spinner-spinner-63r99gwozs2">
                <div class="ldio-jh6epaq1s2b">
                  <div></div>
                  <div></div>
                  <div></div>
                  <div></div>
                  <div></div>
                  <div></div>
                  <div></div>
                  <div></div>
                  <div></div>
                  <div></div>
                  <div></div>
                </div>
              </div>
            )}
          <div id="splash-wrapper">
            {this.state.signUpInitiated === false &&
              this.state.fetchingAuthStatus === false &&
              this.state.doneLoading !== true && (
                <div>
                  <div id="signup-intro">
                    <img
                      id="signup-twitter-icon"
                      alt="twitter-icon"
                      src="./img/twittericon.png"
                    />
                    <h1 id="signup-header">
                      See what's good in the world right now
                    </h1>
                    <h2 id="signup-join">Join Good Twitter today.</h2>
                    <a href="http://localhost:5000/auth/twitter">
                      <button id="signup-button">Sign in with Twitter</button>
                    </a>
                  </div>
                </div>
              )}
          </div>
        </div>
      </div>
    );
  }
}

class LogInForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      email: null,
      password: null
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(event) {
    console.log(event.target.id);
    if (event.target.id === 'login-email-input') {
      this.setState({
        email: event.target.value
      });
    } else if (event.target.id === 'login-password-input') {
      this.setState({
        password: event.target.value
      });
    }
  }

  handleSubmit(event) {
    event.preventDefault();

    logIn(this.state.email, this.state.password);
  }

  render() {
    return (
      <form id="login-form" onSubmit={this.handleSubmit}>
        <input
          id="login-email-input"
          type="email"
          placeholder="E-mail"
          name="email"
          value={this.value}
          onChange={this.handleChange}
          required
        ></input>
        <input
          id="login-password-input"
          type="password"
          placeholder="Password"
          name="password"
          value={this.value}
          onChange={this.handleChange}
          required
        ></input>
        <button
          id="login-button"
          type="submit"
          onClick={() => {
            this.setState({ doneLoading: true });
          }}
        >
          Log in
        </button>
      </form>
    );
  }
}

class SignUpForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      email: null,
      password: null,
      confirmPassword: null
    };
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }
  componentDidMount() {
    document.getElementById('signup-email').focus();
  }

  handleChange(event) {
    console.log(event.target.id);
    if (event.target.id === 'signup-email') {
      this.setState({
        email: event.target.value
      });
    } else if (event.target.id === 'signup-password') {
      this.setState({
        password: event.target.value
      });
    } else if (event.target.id === 'signup-cf-password') {
      this.setState({
        confirmPassword: event.target.value
      });
    }
  }

  handleSubmit(event) {
    event.preventDefault();

    signUpEmail(this.state.email, this.state.password);
  }

  render() {
    return (
      <div id="signup-wrapper">
        <h1 id="signup-menu-header">Create your account</h1>
        <div id="signup-menu">
          <form onSubmit={this.handleSubmit}>
            <label id="signup-email-label">E-mail</label>
            <input
              type="email"
              id="signup-email"
              name="email"
              onChange={this.handleChange}
              value={this.state.value}
            ></input>
            <label id="signup-password-label">Password</label>
            <input
              type="password"
              id="signup-password"
              name="password"
              onChange={this.handleChange}
              value={this.state.value}
            ></input>
            <label id="signup-cf-password-label">Confirm password</label>
            <input
              id="signup-cf-password"
              type="password"
              name="confirm-password"
              onChange={this.handleChange}
              value={this.state.value}
            ></input>
            <div id="back-button" onClick={this.props.toggleMenu}>
              Back
            </div>
            <button id="signup-submit" type="submit">
              Join
            </button>
          </form>
        </div>
      </div>
    );
  }
}

export default LoginSignInScreen;
