import React from 'react';

class ImageGallery extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      imageUrls: this.props.media,
      currentImageUrlIndex: this.props.imageClickIndex
    };
  }

  componentDidMount() {
    this.setImagePadding();
  }

  setImagePadding() {
    const image = document.getElementById('gallery-image');
    const height = image.naturalHeight;
    if (height > 250) {
      image.style.visibility = 'visible';
      image.style.paddingTop = 0;
      return;
    }
    image.style.paddingTop = `${(250 - height) / 2}px`;
    image.style.visibility = 'visible';
  }

  nextImage() {
    const { currentImageUrlIndex, imageUrls } = this.state;
    let number;

    if (imageUrls[currentImageUrlIndex + 1] === undefined) {
      return;
    } else {
      number = currentImageUrlIndex + 1;
    }

    document.getElementById('gallery-image').style.visibility = 'hidden';

    this.setState({
      currentImageUrlIndex: number
    });
    setTimeout(this.setImagePadding, 50);
  }

  previousImage() {
    const { currentImageUrlIndex, imageUrls } = this.state;
    let number;

    if (imageUrls[currentImageUrlIndex - 1] === undefined) {
      return;
    } else {
      number = currentImageUrlIndex - 1;
    }

    this.setState({
      currentImageUrlIndex: number
    });
    setTimeout(this.setImagePadding, 50);
  }

  render() {
    return (
      <div id="gallery-overlay">
        <span
          className="icon-cross-sign"
          onClick={this.props.closeGallery}
        ></span>
        <div id="button-grid">
          <div id="gallery-wrapper">
            <div id="media-content">
              <img
                id="gallery-image"
                alt="gallery-media"
                src={this.state.imageUrls[this.state.currentImageUrlIndex]}
              ></img>
            </div>
            <div id="gallery-footer">
              <div id="gallery-action-bar">
                <div id="left-arrow-wrapper">
                  {this.state.imageUrls[this.state.currentImageUrlIndex - 1] !==
                    undefined && (
                    <span
                      className="icon-left-arrow"
                      onClick={this.previousImage.bind(this)}
                    ></span>
                  )}
                </div>
                <span className="icon-retweet"></span>
                <div id="gallery-retweet-counter">{'155K'}</div>
                <span className="icon-favorite"></span>
                <div id="gallery-favorite-counter">{'2K'}</div>
                {this.state.imageUrls[this.state.currentImageUrlIndex + 1] !==
                  undefined && (
                  <span
                    className="icon-right-arrow"
                    onClick={this.nextImage.bind(this)}
                  ></span>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default ImageGallery;
