import React from 'react';
import './quoted-tweet.css';

class QuotedTweet extends React.Component {
  render() {
    return (
      <a
        target="_blank"
        rel="noopener noreferrer"
        href={`https://twitter.com/${this.props.data.user.screen_name}/status/${this.props.data.id_str}`}
      >
        <div className="quoted-tweet">
          <div className="quoted-tweet-content">
            <h3 className="tweeter-name">{this.props.data.user.name}</h3>
            <p className="tweet-at">@</p>
            <p className="tweet-handle">{this.props.data.user.screen_name}</p>
            <p className="quoted-tweet-text">
              {this.props.data.full_text || this.props.data.text}
            </p>
          </div>
        </div>
      </a>
    );
  }
}

export default QuotedTweet;
