import io from 'socket.io-client';

const socket = io.connect('http://localhost:5000');

async function requestInteractionCount(tweets) {
  let ids = [];
  tweets.forEach(tweet => {
    ids.push(tweet.key);
  });

  const idString = JSON.stringify(ids);

  const response = await fetch('http://localhost:5000/api/interaction_count', {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json'
    },
    body: idString
  });
  const data = await response.json();

  console.log(data);
  return data;
}

async function interactWithTweet(id, interactType) {
  id = id.toString();
  async function favorite(id) {
    const response = await fetch(
      `http://localhost:5000/api/favorite/create/${id}`,
      {
        method: 'POST'
      }
    );
    const data = await response.json();
    return data;
  }

  async function retweet(id) {
    const response = await fetch(
      `http://localhost:5000/api/statuses/retweet/${id}`,
      {
        method: 'POST'
      }
    );

    const data = await response.json();
    return data;
  }

  if (interactType === 'fav') {
    const response = await favorite(id);
    return response;
  } else {
    const response = await retweet(id);
    return response;
  }
}

async function undoTweetInteraction(id, interactType) {
  id = id.toString();
  async function unfavorite(id) {
    const response = await fetch(
      `http://localhost:5000/api/favorite/destroy/${id}`,
      {
        method: 'POST'
      }
    );
    const data = await response.json();
    return data;
  }

  async function undoRetweet(id) {
    const response = await fetch(
      `http://localhost:5000/api/statuses/unretweet/${id}`,
      {
        method: 'POST'
      }
    );

    const data = await response.json();
    return data;
  }

  if (interactType === 'fav') {
    const response = await unfavorite(id);
    return response;
  } else {
    const response = await undoRetweet(id);
    return response;
  }
}

export {
  requestInteractionCount,
  socket,
  interactWithTweet,
  undoTweetInteraction
};
